import Vue from 'vue'
import VueRouter from 'vue-router'
import tab1 from 'views/tabview1/tab1.vue'
// const tab2 = import("views/tabview2/tab2.vue")
import tab2 from 'views/tabview2/tab2.vue'
Vue.use(VueRouter)

const routes = [

  {
    path: '/home',
    name: 'tab1',
    component: tab1
  },
  {
    path: '/',
    // component:tab1,
    redirect: 'home',
  },
  {
    path: '/tab2',
    name: 'tab2',
    component: tab2
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
