<!--  -->
<template>
  <div id="home">
    <header id="header">
      <div id="headerdiv">
        <img id="icon" :src="icon" alt @click="iconClick" />
        <span id="company">河北英曼卡科技(承德)有限公司</span>
        <ul id="tabul">
          <li v-for="(item, index) in tabList" :key="index">
            <!-- <a
              href="javascript:;"
              @click="tabClick(index)"
              :class="{currTab:$store.state.tabIndex==index}"
            >{{item}}</a> -->
            <router-link :to="tabPath[index]"
            :class="{currTab:$store.state.tabIndex==index}"
            @click.native="tabClick(index)">{{item}}</router-link>
          </li>
        </ul>
      </div>
    </header>
  </div>
</template>

<script>
import request from "@/network/network.js";
export default {
  data() {
    return {
      icon: require("assets/images/icon.png"),
      tabPath: ["/home", "/tab2", "/tab3", "/tab4", "/tab5", "/tab6", "/tab7"],
      tabList: [],
      
    };
  },
  created() {

    request({
      url: "html/images/data.json",

    }).then(res => {
      // console.log(res);
      this.$store.state.jsondata = res.data;
      this.tabList = res.data.tab;
      // console.log(this.$store.state.jsondata.swiper);
      
    }).catch(err => {
      alert("请求错误");
    })

  },
  components: {},

  computed: {},

  mounted() {
    if(this.$router.currentRoute.path != this.tabPath[0]){
      this.$router.push(this.tabPath[0]);
    }
  },
  methods: {
    iconClick() {
      console.log("111");
      console.log(this.tabList);
    },
    tabClick(index) {
      if (index != this.$store.state.tabIndex) {
        // this.$router.push(this.tabPath[index]);
        this.$store.state.tabIndex = index;
      }
    }
  }
};
</script>
<style scoped>
@import "~assets/css/home/home.css";
</style>